import AbstractView from "./abstract-view";

export default class ModalErrorView extends AbstractView {
  /**
   *      
   * @param {String} error  
   */
  constructor(error) {
    super();
    this._error = error;
  }

  get template() {
    return `<section class="modal">
      <h2 class="modal__title"> !</h2> 
      <p class="modal__text">${this._error}. ,  .</p>
    </section>`;
  }

  bind() {

  }
}
