import {RoundTimer} from '../constants/constants';

const {RADIX, CIRCLE_FORMULA} = RoundTimer;

/**
 *     
 * @param {Element} timerElement  
 * @param {Number} timeLeft  
 * @param {Number} startTime  ,    
 */
export default (timerElement, timeLeft, startTime) => {
  const radius = parseInt(timerElement.getAttribute(`r`), RADIX); //  
  const circumference = Math.floor(CIRCLE_FORMULA * radius); //  
  const offset = circumference - ((startTime - timeLeft) / startTime) * circumference; //    
  const timerElementClassList = [...timerElement.classList].join(`.`); //    
  const style = document.createElement(`style`); //     style

  timerElement.setAttribute(`stroke-dasharray`, offset.toString(RADIX)); //    
  style.innerHTML = `
  ${timerElement.tagName}.${timerElementClassList}
   {transition: ${timeLeft}s stroke-dashoffset linear; will-change: stroke-dashoffset;}
   `; //  transition c  
  timerElement.appendChild(style); //   style
  setTimeout(() => timerElement.setAttribute(`stroke-dashoffset`, circumference), 100); //    
};
