import AbstractView from "./abstract-view";

export default class ModalConfirmView extends AbstractView {
  /**
   *      
   */
  constructor() {
    super(`section`, [`modal`, `modal--hidden`]);
  }

  /**
   * 
   * @return {string}   
   */
  get template() {
    return `
<button class="modal__close js-cancel" type="button"><span class="visually-hidden"></span></button>
<h2 class="modal__title"></h2>
<p class="modal__text">      ?</p>
<div class="modal__buttons">
  <button class="modal__button button js-confirm"></button>
  <button class="modal__button button js-cancel"></button>
</div>`;
  }

  /**
   *    
   */
  show() {
    this.element.classList.remove(`modal--hidden`);
  }

  /**
   *    
   */
  hide() {
    this.element.classList.add(`modal--hidden`);
  }

  /**
   *  
   */
  bind() {
    //  
    const cancelButtons = this.element.querySelectorAll(`.js-cancel`);
    cancelButtons.forEach((button) => button.addEventListener(`click`, () => {
      event.preventDefault();
      this.onCancel();
    }));
    //  
    const confirmButtons = this.element.querySelectorAll(`.js-confirm`);
    confirmButtons.forEach((button) => button.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      this.onConfirm();
    }));
  }

  /**
   *    
   */
  onConfirm() {

  }

  /**
   *    
   */
  onCancel() {

  }
}
