import AbstractView from "./abstract-view";

export default class WelcomeView extends AbstractView {
  /**
   *     
   */
  constructor() {
    super();
  }

  /**
   *  
   * @return {string}   
   */
  get template() {
    return `
<section class="welcome">
  <div class="welcome__logo"><img src="img/melody-logo.png" alt=" " width="186" height="83"></div>
  <button class="welcome__button welcome__button--loading" disabled><span class="visually-hidden"> </span></button>
  <h2 class="welcome__rules-title"> </h2>
  <p class="welcome__text"> :</p>
  <ul class="welcome__rules-list">
    <li> 5      .</li>
    <li>  3 .</li>
  </ul>
  <p class="welcome__text">!</p>
</section>
`;
  }

  /**
   *    
   */
  enableStartGame() {
    this._buttonPlay.removeAttribute(`disabled`);
    this._buttonPlay.classList.remove(`welcome__button--loading`);
  }

  /**
   *  
   */
  bind() {
    //  
    this._buttonPlay = this.element.querySelector(`.welcome__button`);
    this._buttonPlay.addEventListener(`click`, (evt) => {
      evt.preventDefault();
      this.onStartGame();
    });
  }

  /**
   *    
   */
  onStartGame() {

  }
}
