import AbstractEndGameView from './abstract-end-game-view';
import {convertSecondsToHumanReadableString, convertMistakesToPluralString} from '../services/utils';

export default class SuccessView extends AbstractEndGameView {
  /**
   *      
   * @param {Object} data   
   */
  constructor(data) {
    super();
    Object.assign(this, data); //   data  
  }

  /**
   *           
   * @return {String}      
   */
  get timeText() {
    return convertSecondsToHumanReadableString(this.time);
  }

  /**
   *       
   * @return {String}       
   */
  get mistakesText() {
    return convertMistakesToPluralString(this.mistakes);
  }

  /**
   * 
   * @return {string}  
   */
  get template() {
    return `<section class="result">
  <div class="result__logo"><img src="img/melody-logo.png" alt=" " width="186" height="83"></div>
  <h2 class="result__title">  !</h2>
  <p class="result__total"> ${this.timeText}   ${this.score}  (${this.bonusScore} ),  ${this.mistakesText}</p>
  <p class="result__text">${this.rating}</p>
  <button class="result__replay" type="button">  </button>
</section>`;
  }
}
